}


/*--------------------------
    Banner Style 6  
---------------------------*/

.slider-style-6 {
    padding: 15px;
    background: var(--background-color-2);
    border-radius: 5px;

    &.wide-wrapper {
        margin: 0 -110px;

        @media #{$laptop-device} {
            margin: 0;
        }

        @media #{$lg-layout} {
            margin: 0;
        }

        @media #{$md-layout} {
            margin: 0;
        }

        @media #{$sm-layout} {
            margin: 0;
        }
    }

    .slide {
        position: relative;
        z-index: 1;
        min-height: 476px;
        display: flex;
        align-items: center;

        @media #{$sm-layout} {
            min-height: auto;
            padding: 50px 0;
        }

        .banner-read-thumb-lg {
            padding: 20px 50px;

            @media #{$sm-layout} {
                padding: 20px 30px;
            }

            h4 {
                margin-bottom: 15px;
                font-size: 50px;
                font-weight: 400;

                @media #{$md-layout} {
                    font-size: 36px;
                }

                @media #{$sm-layout} {
                    font-size: 36px;
                }

                @media #{$large-mobile} {
                    font-size: 26px;
                }

            }

            p {
                max-width: 60%;
                margin-bottom: 30px;
                color: var(--color-body);

                @media #{$smlg-device} {
                    max-width: 70%;
                }

                @media #{$md-layout} {
                    max-width: 80%;
                }

                @media #{$sm-layout} {
                    max-width: 100%;
                    font-size: 18px;
                    margin-bottom: 19px;
                }

                @media #{$small-mobile} {
                    max-width: 100%;
                }
            }
        }
    }

    &.slick-arrow-between {
        button {
            &.slide-arrow {
                &.prev-arrow {
                    left: 0px !important;
                }

                &.next-arrow {
                    right: 0px !important;
                }
            }
        }
    }


}



/*--------------------------
    Banner Style 7 
---------------------------*/

.slider-style-7 {
    height: auto;
    border-radius: 8px;
    padding: 90px 0;
    overflow-y: hidden;

    .rn-banner-wrapper {
        width: 100%;
        padding: 0 30px;
        display: flex;
        align-items: center;

        @media #{$small-mobile} {
            margin: 0;
            padding: 0 5px;
        }

        .item-description {
            p {
                margin-bottom: 30px;
                color: var(--color-body);
            }

            .product-share-wrapper {
                margin-top: 0;
                flex-wrap: wrap;

                .profile-share {
                    a {
                        &.more-author-text {
                            color: #acacac;
                        }
                    }
                }

                .countdown {
                    @media #{$large-mobile} {
                        margin-top: 10px;
                    }
                }
            }
        }

        h2 {
            &.title {
                font-size: 40px;
                margin-bottom: 0;

                @media #{$lg-layout} {
                    margin-bottom: 20px;
                }

                @media #{$md-layout} {
                    margin-bottom: 20px;
                }

                @media #{$sm-layout} {
                    margin-bottom: 20px;
                    font-size: 30px;
                }

                span {
                    color: var(--color-primary);
                }
            }
        }

        .img-thumb-award {
            text-align: right;

            @media #{$lg-layout} {
                text-align: left;
                margin-bottom: 15px;
            }

            @media #{$md-layout} {
                text-align: left;
                margin-bottom: 15px;
            }

            @media #{$sm-layout} {
                text-align: left;
                margin-bottom: 15px;
            }

            img {
                max-width: 130px;
            }
        }
    }
}


