.banner-gallery-wrapper {
    max-height: 700px;
    overflow: hidden;
    position: relative;

    @media #{$md-layout} {
        max-height: 450px;
    }

    @media #{$sm-layout} {
        max-height: 350px;
    }

    &::before {
        position: absolute;
        width: 100%;
        height: 30%;
        left: 0;
        bottom: 0;
        content: "";
        background-image: linear-gradient(to bottom, #0c0c2100 0%, #171721 95%);
        z-index: 1;
        pointer-events: none;
    }

    &::after {
        position: absolute;
        width: 100%;
        height: 30%;
        left: 0;
        top: 0;
        content: "";
        background-image: linear-gradient(to top, #0c0c2100 0%, #171721 95%);
        z-index: 1;
        pointer-events: none;
    }

    .banner-gallery-loop {
        display: flex;
        align-items: center;
        margin: 0 -20px;
    }

    .banner-gallery {
        transform: translate3d(0, 0, 0);
        transition: opacity 0.6s cubic-bezier(0.215, 0.61, 0.355, 1);
        padding: 0 20px;
        width: 280px;

        img {
            transition: opacity 0.6s cubic-bezier(0.215, 0.61, 0.355, 1);
            display: block;
            width: 100%;
            margin-bottom: 30px;
        }
    }

    .banner-gallery-1 {
        -webkit-animation: verticalSlide 115s linear infinite;
        animation: verticalSlide 90s linear infinite;
    }

    .banner-gallery-2 {
        -webkit-animation: verticalSlide 115s linear infinite;
        animation: verticalSlide 70s linear infinite;
    }

    .banner-gallery-3 {
        -webkit-animation: verticalSlide 115s linear infinite;
        animation: verticalSlide 65s linear infinite;
    }

    .banner-gallery-4 {
        -webkit-animation: verticalSlide 115s linear infinite;
        animation: verticalSlide 80s linear infinite;
    }

    .banner-gallery-5 {
        -webkit-animation: verticalSlide 115s linear infinite;
        animation: verticalSlide 50s linear infinite;
    }
}

@-webkit-keyframes verticalSlide {
    to {
        -webkit-transform: translate3d(0, -50%, 0);
        transform: translate3d(0, -50%, 0);
    }
}

@keyframes verticalSlide {
    to {
        -webkit-transform: translate3d(0, -50%, 0);
        transform: translate3d(0, -50%, 0);
    }
}




